.product-section {
  text-align: center;
  padding: 50px;
  background-color: #ffffff;
  position: relative;
}
.trending-products{
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.product-section h2 {
  color: #003E90;
  font-family: 'Quicksand';
  font-size: 60px;
  font-weight: 700;
  line-height: 60px;
  position: relative; /* Allow positioning of the image */
}

.product-section span {
  position: relative; /* For positioning the underline image */
  z-index: 1; /* Ensure the text is above the underline image */
}

.product-section .underline-image2 {
  display: block; /* Ensure it behaves like a block element */
margin: 0 auto; 
width: 0; /* Start with a width of 0 for animation */
height: 10px;
height: 12px; /* Set the height as required */
position: absolute; 
top: 55px; /* Position relative to h2 */
left: 67.5%; /* Center the image horizontally */
transform: translateX(-50%); /* Adjust for centering */
border-radius: 45px; /* Maintain rounded edges */
background-color: #FF4E00; /* Background color for visibility */
animation: growUnderline2 6s ease-out infinite; 
}

@keyframes growUnderline2 {
  0%, 100% {
      width: 0; /* Start and end at width 0 */
  }
  50% {
      width: 227px; /* Reach max width at 50% of the animation */
  }
}

.product-section p {
  width: 647.81px;
  font-size: 18px;
  line-height: 32px;
  font-weight: 400;
  text-align: center;
  font-family: 'Quicksand';
  color: #1F1F1F;
  margin: 0 auto 30px;
}

.product-section::before {
  content: '';
  background: url('../../Image/home/globe1.png') no-repeat;
  position: absolute;
  top: 20px;
  left: 10px;
  width: 100px;
  height: 100px;
  opacity: 1;
}

.product-section::after {
  content: '';
  background: url('../../Image/home/car1.png') no-repeat;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100px;
  height: 100px;
  opacity: 1;
}

/* Media Queries for responsiveness */
@media (max-width: 1024px) {
  .product-section h2 {
    color: #003E90;
    font-family: 'Quicksand';
    font-size: 45px;
    font-weight: 700;
    line-height: 60px;
    position: relative; /* Allow positioning of the image */
  }
  .product-section .underline-image2 {
    display: block; /* Ensure it behaves like a block element */
  margin: 0 auto; 
  width: 0; /* Start with a width of 0 for animation */
  height: 10px;
  height: 12px; /* Set the height as required */
  position: absolute; 
  top: 52px; /* Position relative to h2 */
  left: 72.5%; /* Center the image horizontally */
  transform: translateX(-50%); /* Adjust for centering */
  border-radius: 45px; /* Maintain rounded edges */
  background-color: #FF4E00; /* Background color for visibility */
  animation: growUnderline2 6s ease-out infinite; 
  }
  
  @keyframes growUnderline2 {
    0%, 100% {
        width: 0; /* Start and end at width 0 */
    }
    50% {
        width: 180px; /* Reach max width at 50% of the animation */
    }
  }
  .product-section p {
    width: 647.81px;
    font-size: 14px;
    line-height: 32px;
    font-weight: 400;
    text-align: center;
    font-family: 'Quicksand';
    color: #1F1F1F;
    margin: 0 auto 30px;
  }
  .product-section {
    padding: 40px;
  }
  
  .product-section h2 {
    font-size: 50px; 
  }
  
  .product-section p {
    width: 90%; /* Adjust paragraph width for tablets */
  }

  
}

@media (max-width: 768px) {
  .product-section h2 {
    color: #003E90;
    font-family: 'Quicksand';
    font-size: 40px;
    font-weight: 700;
    line-height: 60px;
    position: relative; /* Allow positioning of the image */
  }
  .product-section .underline-image2 {
    display: block;
    margin: 0 auto;
    width: 0;
    height: 12px; /* Fixed duplicate height */
    position: absolute;
    top: 41px;
    left: 83.5% !important;
    transform: translateX(-50%);
    border-radius: 45px;
    background-color: #FF4E00;
    animation: growUnderline2 6s ease-out infinite;
  }
  
  @keyframes growUnderline2 {
    0%, 100% {
        width: 0; /* Start and end at width 0 */
        height: 5px;
    }
    50% {
        width: 120px; /* Reach max width at 50% of the animation */
        
    }
  }
  .product-section {
    padding: 30px; /* Adjust padding for smaller tablets */
  }
  
  .product-section h2 {
    font-size: 40px; 
  }
  
  .product-section p {
    width: 80%; /* Adjust paragraph width for smaller tablets */
  }

 
}

@media (max-width: 575px) {
  .trending-products{
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
  }
  .product-section {
    padding: 20px; /* Adjust padding for mobile */
  }
  
  .product-section h2 {
    font-size: 30px; 
  }
  
  .product-section p {
    width: 100%; /* Full width for mobile */
    font-size: 14px;
  }

  
}

